.about {
  display: flex;
  margin-top: 104px;
  flex-direction: column;

  @media (min-width: 768px) {
    & {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      padding-left: 10vw;
      padding-right: 10vw;
    }
  }

  &__title {
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -1.5px;
    color: #000;
  }

  &__second-title {

    @media (min-width: 768px) {
      & {
        width: 50%;
      }
    }
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
  }

  &__article {
    @media (min-width: 768px) {
      & {
        width: 50%;
      }
    }
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
  }
}
